import React from 'react'

import NavButton from './nav-button'
import { Twitter, Home } from './icons'

import styles from './navigation.module.css'
function Navigation() {
  return (
    <button type="button" className={styles.button}>
      <nav>
        <NavButton>
          <Twitter />
        </NavButton>
        <NavButton>
          <Home />
          <span>Home</span>
        </NavButton>
      </nav>
    </button>
  )
}

export default Navigation
